import React, { useState } from 'react';
import {
  Box,
  Button,
  Flex,
  FormControl,
  FormLabel,
  Heading,
  Input,
  Link,
  Stack,
  Text,
  useToast,
} from '@chakra-ui/react';
import colors from '../../theme/colors'; // Importando as cores padro

const PasswordRecovery: React.FC = () => {
  const [email, setEmail] = useState('');
  const [code, setCode] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [step, setStep] = useState(1);
  const toast = useToast();

  const handleSendEmail = () => {
    // Simulao de envio de e-mail com o cdigo de recuperao
    if (email) {
      // Simulao de envio de e-mail bem-sucedido
      toast({
        title: 'E-mail de recuperao enviado',
        description: `Um e-mail de recuperao foi enviado para ${email}.`,
        status: 'success',
        duration: 3000,
        isClosable: true,
      });
      setStep(2);
    } else {
      // Exibir mensagem de erro se nenhum e-mail for fornecido
      toast({
        title: 'Erro ao enviar e-mail',
        description: 'Por favor, fornea um endereo de e-mail vlido.',
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    }
  };

  const handleVerifyCode = () => {
    // Simulao de verificao do cdigo de recuperao
    if (code === '123456') {
      setStep(3);
    } else {
      // Exibir mensagem de erro se o cdigo estiver incorreto
      toast({
        title: 'Cdigo invlido',
        description: 'O cdigo inserido  invlido. Por favor, tente novamente.',
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    }
  };

  const handleResetPassword = () => {
    // Simulao de redefinio de senha
    // Verifica se a senha e a confirmao da senha so iguais
    if (password === confirmPassword) {
      toast({
        title: 'Senha redefinida',
        description: 'Sua senha foi redefinida com sucesso.',
        status: 'success',
        duration: 3000,
        isClosable: true,
      });
      // Reiniciar os campos e voltar para a primeira etapa
      setEmail('');
      setCode('');
      setPassword('');
      setConfirmPassword('');
      setStep(1);
    } else {
      // Exibir mensagem de erro se as senhas no coincidirem
      toast({
        title: 'Senhas no coincidem',
        description: 'A senha e a confirmao da senha no so iguais. Por favor, tente novamente.',
        status: 'error',
        duration: 3000,
        isClosable: true,
      });
    }
  };

  const renderStep = () => {
    switch (step) {
      case 1:
        return (
          <FormControl id="email" isRequired>
            <FormLabel>E-mail</FormLabel>
            <Input
              type="email"
              placeholder="Digite seu e-mail"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <Button mt={4} colorScheme="teal" bgColor={colors.primary[500]} color="white" onClick={handleSendEmail}>
              Enviar e-mail de recuperao
            </Button>
          </FormControl>
        );
      case 2:
        return (
          <FormControl id="code" isRequired>
            <FormLabel>Cdigo de Recuperao</FormLabel>
            <Input
              type="text"
              placeholder="Digite o cdigo enviado por e-mail"
              value={code}
              onChange={(e) => setCode(e.target.value)}
            />
            <Button mt={4} colorScheme="teal" bgColor={colors.primary[500]} color="white" onClick={handleVerifyCode}>
              Verificar Cdigo
            </Button>
          </FormControl>
        );
      case 3:
        return (
          <FormControl id="password" isRequired>
            <FormLabel>Nova Senha</FormLabel>
            <Input
              type="password"
              placeholder="Digite sua nova senha"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <FormLabel mt={4}>Confirmar Nova Senha</FormLabel>
            <Input
              type="password"
              placeholder="Confirme sua nova senha"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            />
            <Button mt={4} colorScheme="teal" bgColor={colors.primary[500]} color="white" onClick={handleResetPassword}>
              Redefinir Senha
            </Button>
          </FormControl>
        );
      default:
        return null;
    }
  };

  return (
    <Flex
      minH={'100vh'}
      align={'center'}
      justify={'center'}
      bg={colors.primary[50]}
    >
      <Stack spacing={8} mx={'auto'} maxW={'md'} py={12} px={6}>
        <Stack align={'center'}>
          <Heading fontSize={'4xl'} color={colors.primary[700]}>Recuperao de Senha</Heading>
        </Stack>
        <Box
          rounded={'lg'}
          bg={'white'}
          boxShadow={'lg'}
          p={8}
        >
          {renderStep()}
        </Box>
        <Text align={'center'}>
          Lembrou da senha?{' '}
          <Link color={colors.primary[500]} href={'/'}>
            Faa Login
          </Link>
        </Text>
      </Stack>
    </Flex>
  );
};

export default PasswordRecovery;
